.LandingPage-main {
  margin-left: 25%;
  margin-top: 12%;
  text-align: center;
  height: 50%;
  width: 50%;
  border-color: black;
  border-width: thick;
}

.LandingPage-logo {
  -webkit-animation: LandingPage-logo-spin infinite 20s linear;
          animation: LandingPage-logo-spin infinite 20s linear;
  height: 80px;
}

.LandingPage-header {
  background-color: #7A7C7B;
  height: 15%;
  padding: 20px;
  color: white;
}

.LandingPage-title {
  font-size: 1.5em;
}

.LandingPage-intro {
  font-size: large;
}

.NameEnter-btn {

}

.StartGame-btn {

}

@-webkit-keyframes LandingPage-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes LandingPage-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



